@import url("https://fonts.googleapis.com/css2?family=ADLaM+Display&family=Kdam+Thmor+Pro&family=Monoton&family=Poppins:ital,wght@0,400;0,500;0,600;1,400;1,500&family=Style+Script&family=Yatra+One&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "poppins", sans-serif;
}

html,
body {
  width: 100%;
  height: 100vh;
}

.parent-Container {
  position: relative;
  width: 100%;
  height: calc(100vh - 10vh);
  display: flex;
  justify-content: center;
  align-items: center;
}

.auth-container {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: clamp(200px, 80%, 700px);
  height: 50vh;
  border-radius: 1rem;
  overflow: hidden;
  border-top: 1px solid rgba(255, 255, 255, 0.612);
  border-left: 1px solid rgba(255, 255, 255, 0.612);
  z-index: 2;
  backdrop-filter: blur(10px);
  background: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0.255),
    rgba(255, 255, 255, 0.07)
  );
}

.parent-Container::after,
.parent-Container::before {
  content: "";
  position: absolute;
  width: 20rem;
  height: 20rem;
  background: linear-gradient(
    to right,
    rgb(255, 72, 0),
    rgba(255, 255, 255, 0.182)
  );
  backdrop-filter: blur(20px);
  border-radius: 50%;
  left: 15vw;
  top: 6vw;
}

.parent-Container::before {
  width: 10rem;
  height: 10rem;
  left: 70vw;
  top: 6vw;
  background: linear-gradient(
    to bottom,
    rgb(255, 72, 0),
    rgba(255, 255, 255, 0)
  );
  z-index: 3;
}

.Login-container,
.register-container {
  width: clamp(300px, 50%, 50%);

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 1vw;
  flex-grow: 1;
}
.Login-container h1,
.register-container h1 {
  color: rgba(255, 255, 255, 0.703);
}

form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1vw;
}

form h1 {
  font-size: 1.5rem;
}

input {
  display: block;
  height: 5vh;
  width: 100%;
  border: none;
  border-radius: 0.5rem;
  padding: 0 2vw;
}
.submit {
  color: black;
  background-color: aqua;
}

form .submit:hover {
  background-color: #00c227;
}
